import { useRecoilValue } from 'recoil';
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Typography,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

import { foodAmountState } from '../../costsComponent/state';

const FoodDetailAmount = ({ 
  expanded,
  handleChange, 
  index 
}) => {
  const amounts = useRecoilValue(foodAmountState)

  return (
    <Accordion 
      expanded={expanded === index} 
      onChange={handleChange(index)}
    >
      {/*   */}
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls={`panel${index}-content`}
        id={`panel${index}-header`}
      >
        {/*   */}
        <Typography sx={{ width: '50%', flexShrink: 0 }}>
          {amounts.foodPerMonthRounded} kg
        </Typography>

        {/*   */}
        <Typography sx={{ color: 'text.secondary' }}>
            
        </Typography>
      </AccordionSummary>
      
      {/*    */}
      <AccordionDetails>
        <ul>
          <li>
            <Typography>
               * 2% =   
            </Typography>
          </li>
          <li>
            <Typography>
              {amounts.weightAvg} kg * 2% 
              = {amounts.foodPerDayAsGram} g
            </Typography>
          </li>
          <li>
            <Typography>
              30 * {amounts.foodPerDayAsGram} g 
              = {amounts.foodPerMonthRounded} kg
            </Typography>
          </li>
        </ul>
      </AccordionDetails>
    </Accordion>
  );
}

export default FoodDetailAmount;