import React from 'react';
import { useLocation, Link } from 'react-router-dom';
import { Divider, Button } from '@mui/material';

import ResultHeader from './resultComponent/resultHeader';
import ResultBody from './resultComponent/resultBody';
import './result.css'

const Result = () => {
  //      (Array) 
  const { state } = useLocation();
  // console.log(state);

  return (
    <div>
      <div className='result-arrange'>
        <ResultHeader />
      </div>
      <Divider variant='middle' />
      <ResultBody dogData={ state } />

      <div className='result-retest'>
        {/*   */}
        <Button 
          variant='text'
          style={{color: 'gray'}}
          component={Link}
          to='/recommend'
        > </Button>
        {/*   */}
        {/* <Link className='result-retest-link' to={ '/test' }> </Link> */}
      </div>
    </div>
  );
};

export default Result;